/*HOME PAGE*/

video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
  position: absolute;
  right: 0;
  bottom: 0;
  background-position: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin-bottom: 0;
}

.video-container {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  box-shadow: inset 0 0 0 100px rgba(0, 0, 0.2) object-fit contain;
  margin: 0 auto;
}

.video-container > h1 {
  color: white;
  font-size: 100px;
  margin-top: -100px;
}

.video-container > h2 {
  color: white;
  font-size: 50px;
  margin-top: 0;
}

.video-container > p {
  color: white;
  margin-top: 8px;
  font-size: 30px;
  font-family: "Trebuchet MS", "Lucida Sans Unicode" "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

@media screen and (max-width: 960px) {
  .video-container > h1 {
    font-size: 70px;
    margin-top: -150px;
  }
}
@media screen and (max-width: 760px) {
  .video-container > h1 {
    font-size: 50px;
    margin-top: 100px;
    .video-container > p {
      font-size: 20px;
    }
  }
}

.Rest {
  text-align: center;
