}

.rooms {
  /*padding: 8vw 8vw 0 8vw;*/
  text-align: center;
  color: rgb(44, 44, 80);
  margin-top: none;
}

.rooms h1 {
  margin-top: 0;
  font-size: 40px;
  text-align: left;
}

.rooms p {
  text-align: left;
  font-size: 20px;
}

.rooms .room-box {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(340px, 1fr));
  grid-gap: 1rem;
  margin-top: 20px;
  margin-bottom: 50px;
  text-align: right;
}

.room-box .room h6 {
  margin-bottom: 0;
  margin-top: 0;
  font-size: large;
}

.room-box .room {
  text-align: start;
  background: white;
  height: 80%;
  margin-bottom: 0%;
  margin-top: 0%;
}

.room-box .room .lk {
  display: flex;
  gap: 25px;
  margin-top: 20px;
  margin-bottom: 10px;
  align-items: center;
  margin-left: 8px;
  overflow: hidden;
}

.room-box .room .lk .like-button {
  padding: 8px 12px;
  border: none;
  outline: none;
  color: white;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 12px;
}

.room-box .room .lk .like-button::after {
  content: "";
  z-index: -1;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #333;
  left: 0;
  top: 0;
  border-radius: 10px;
}

.room-box .room .lk .like-button::before {
  content: "";
  background: linear-gradient(
    #ff0000,
    #ff7300,
    #fffb00,
    #48ff00,
    #00ffd5,
    #002bff,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 600%;
  z-index: -1;
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  filter: blur(8px);
  animation: glowing 20s linear infinite;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
  opacity: 1;
}

@keyframes glowing {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

/* Dislike Button */
.room-box .room .lk .dislike-button {
  background-color: #f44336;
  color: white;
  font-size: 14px;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  padding: 8px 12px;
  outline: none;
  position: relative;
  z-index: 0;
}

.room-box .room .lk .dislike-button:hover {
  background-color: #da190b;
}

/* Buy Button */
