/* Flex container for centering and spacing */
.payment-container {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

/* Payment form layout */
.payment-form {
  display: flex; /* Use flexbox for layout */
  justify-content: space-between; /* Space items evenly */
  width: 100%;
  max-width: 800px; /* Adjust max-width to fit both sections comfortably */
  padding: 20px;
  background-color: #f5f5f5; /* Light background color */
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* Subtle box shadow */
  margin-right: 300px;
}

/* Billing Address section */
.billing-address {
  flex: 1; /* Take up remaining space */
  margin-right: 20px; /* Margin between sections */
}

/* Payment Details section */
.payment-details {
  flex: 1; /* Take up remaining space */
}

/* General form group styling */
.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
}

.form-group input[type="text"],
.form-group input[type="email"],
.form-group input[type="number"],
.form-group select {
  width: 100%;
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

/* Submit button styling */
.submit-button {
  display: block;
  width: 100%;
  padding: 10px;
  background-color: #4caf50; /* Green button background */
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease; /* Smooth hover transition */
}

.submit-button:hover {
  background-color: #45a049; /* Darker green on hover */
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .payment-form {
    flex-direction: column; /* Stack items vertically on smaller screens */
  }

  .billing-address,
  .payment-details {
    margin-right: 0; /* Remove margin between sections on smaller screens */
  }
}
.video-tours {
  padding: 50px 20px;
  background-color: #fff;
}

.videos-grid {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.video-item {
  margin: 10px;
  text-align: center;
}

.video-item iframe {
  width: 100%;
  max-width: 560px;
  height: 315px;
}
/* Container for the room boxes */
.room-box {
  display: flex;
  justify-content: center; /* Center the items horizontally */
  gap: 40px; /* Increase gap between room items */
  flex-wrap: wrap; /* Allow wrapping for smaller screens */
  margin-top: 40px; /* Add space at the top */
  padding: 0 20px; /* Add padding to give space from the sides of the browser */
}

/* Individual room style */
.room {
  width: 500px; /* Set a fixed width for each room box */
  position: relative;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.room img {
  width: 100%;
  height: auto;
  display: block;
}

.room h6 {
  padding: 10px;
  font-size: 18px;
  text-align: center;
  background-color: #f8f9fa;
  margin: 0;
}

/* Style for the icon container */
.list-icon-content {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 5px 10px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.list-icon img {
  height: 20px;
  width: 20px;
}

.list-icon p {
  color: #fff;
  font-size: 16px;
  margin: 0;
}

.room:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
}

/* Container for buttons */
.lk {
  display: flex;
  justify-content: space-around; /* Distribute buttons evenly */
  padding: 10px;
  background-color: #f8f9fa;
  margin-top: 20px; /* Add margin to create space between the room boxes and buttons */
}

/* Button styles */
button {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 20px; /* Increase padding for larger button size */
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0056b3;
}

/* Specific button styles */
.like-button {
  background-color: #28a745;
}

.like-button:hover {
  background-color: #218838;
}

.dislike-button {
  background-color: #dc3545;
}

.dislike-button:hover {
  background-color: #c82333;
}

.buy-button {
  background-color: #ffc107;
  color: #212529;
}

.buy-button:hover {
  background-color: #e0a800;
}

.rent-button {
  background-color: #17a2b8;
}

.rent-button:hover {
  background-color: #138496;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .room {
    width: calc(50% - 20px); /* Adjust width for tablets */
  }
}

@media (max-width: 480px) {
  .room {
    width: 100%; /* Full width for mobile devices */
  }

  .room-box {
    gap: 20px; /* Reduce gap for smaller screens */
  }

  .lk {
    flex-direction: column; /* Stack buttons vertically on small screens */
    gap: 10px;
  }

  button {
    width: 100%; /* Full width buttons on mobile */
  }
}
